import React from "react";
import SimpleImageSlider from "react-simple-image-slider";

export function Slider(): JSX.Element {
  const productImages = [
    {
      url:
        "https://res.cloudinary.com/kruzabasi/image/upload/v1616061201/BMH/large_12_2_taxomu.png"
    },
    {
      url:
        "https://res.cloudinary.com/kruzabasi/image/upload/v1616060872/BMH/large_15_2_hwbp6r.png"
    }
  ];

  return (
    <div className="product-image">
      {" "}
      <SimpleImageSlider
        width={365}
        height={373}
        images={productImages}
        showNavs={true}
        showBullets={true}
        bgColor={"00000000"}
        navStyle={2}
      />
    </div>
  );
}
