import React, { FormEvent, MouseEvent, useState } from "react";
import Button from "./Button";
import { Link } from "react-router-dom";
import arrow from "../assets/Path4.svg";
import close from "../assets/close-512.png";
import "./styles.css";

export function Planning(): JSX.Element {
  //component hooks
  const [showModal, setShowModal] = useState(false);
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [telephone, setTelephone] = useState("");
  const [landLocation, setLandLocation] = useState("");
  const [dimension, setDimension] = useState("");
  const [units, setUnits] = useState("");
  const [building, setBuilding] = useState("");
  const [budget, setBudget] = useState("");
  const [notes, setNotes] = useState("");

  //form field handlers
  function handleFullName(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setFullName(e.target.value);
  }
  function handleEmail(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setEmail(e.target.value);
  }
  function handlePhone(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setTelephone(e.target.value);
  }
  function handleLocation(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setLandLocation(e.target.value);
  }
  function handleDimension(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setDimension(e.target.value);
  }
  function handleUnits(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setUnits(e.target.value);
  }
  function handleBuilding(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setBuilding(e.target.value);
  }
  function handleBudget(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setBudget(e.target.value);
  }
  function handleNotes(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setNotes(e.target.value);
  }

  //handle submit event
  const handleSubmit = (e: FormEvent): void => {
    e.preventDefault();

    //reset form fields on success
    setShowModal(true);
    setFullName("");
    setEmail("");
    setTelephone("");
    setLandLocation("");
    setDimension("");
    setUnits("");
    setBuilding("");
    setBudget("");
    setNotes("");
  };
  const closeModal = (e: MouseEvent): void => {
    setShowModal(false);
  };
  const modalClass = showModal ? "planning-modal" : "hide-modal";
  return (
    <div className="planningPage">
      <div className="planBanner">
        <Link to="/build">
          <img src={arrow} alt="back-arrow" className="signup-back-arrow" />
        </Link>
        <h3>Planning</h3>
      </div>

      <section>
        In order to achieve a fully custom build, our Architects work closely
        with you from locating or inspecting your lot to creating the
        blueprints. <br></br>
        <br></br>
        To fully understand where you are with your building process, we require
        the following form be filled out and submitted.
      </section>
      <form className="planningForm" onSubmit={handleSubmit}>
        <input
          type="text"
          name="FullName"
          className="inputTextField"
          placeholder="Full name"
          required
          value={fullName}
          onChange={handleFullName}
        />
        <input
          type="email"
          name="email"
          className="inputTextField"
          placeholder="Email"
          required
          value={email}
          onChange={handleEmail}
        />
        <input
          type="text"
          name="telephone"
          className="inputTextField"
          placeholder="Telephone"
          required
          value={telephone}
          onChange={handlePhone}
        />
        <select
          name="location"
          className="inputSelectField"
          required
          value={landLocation}
          onChange={handleLocation}
        >
          <option>Location of land</option>
          <option value="Abuja">Abuja</option>
          <option value="Lagos">Lagos</option>
          <option value="Port-Hacourt">Port-Hacourt</option>
        </select>
        <div className="subSection">
          <input
            type="text"
            name="dimension"
            className="inputTextField"
            placeholder="Dimension"
            required
            value={dimension}
            onChange={handleDimension}
          />
          <select
            name="unit"
            className="inputSelectField"
            required
            value={units}
            onChange={handleUnits}
          >
            <option>Units</option>
          </select>
        </div>
        <input
          type="text"
          name="buildingInterest"
          className="inputTextField"
          placeholder="Building of interest"
          required
          value={building}
          onChange={handleBuilding}
        />
        <input
          type="text"
          name="budget"
          className="inputTextField"
          placeholder="Budget"
          required
          value={budget}
          onChange={handleBudget}
        />
        <div className="subSection text-section">
          <label htmlFor="notes">Additional notes:</label>
          <textarea
            name="notes"
            value={notes}
            onChange={handleNotes}
          ></textarea>
        </div>
        <Button />
      </form>
      <div className={modalClass}>
        <button onClick={closeModal}>
          <Link to="/build">
            <img src={close} />
          </Link>
        </button>
        <h2>
          Your Enquiry Has Been Sent Successfully, Kindly Await Our Response
        </h2>
      </div>
    </div>
  );
}
