import React, { ChangeEvent, FormEvent, useEffect, useState } from "react";
import "./styles.css";
import { Input } from "antd";
import searchIcon from "../assets/search-icon.svg";
import { Link } from "react-router-dom";
import FeatureCard from "./FeatureCard";
import FooterIcon from "../../footericon/components/FooterIcon";
import { Feature, IFeatureCardProps } from "./types";
import { IFeatureData } from "../../common/types";
import Loader from "../../Loader";

function BuildItScreen({}: IFeatureCardProps): JSX.Element {
  const [searchValue, setSearchValue] = useState("");
  const [loading, setLoading] = useState(true);
  const [featureDisplay, setFeatureDisplay] = useState<IFeatureData | null>(
    null
  );
  const handleChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setSearchValue(e.target.value);
  };

  const handleSubmit = (e: FormEvent): void => {
    e.preventDefault();
  };

  const handleDisplay = (feature: Feature) => {
    const url =
      "https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql?query=";
    const query = `{
      products(name: "${feature}"){
        id
        name
        userId
        imageCollection{
          url
          name
          id
        }
      }
    }`;
    fetch(`${url}${query}`)
      .then(resp => resp.json())
      .then(d => {
        setLoading(false);
        setFeatureDisplay(d.data.products[0]);
      });
  };
  useEffect(() => {
    handleDisplay;
  });

  return (
    <div className="container">
      <form onSubmit={handleSubmit} className="feature-form">
        <Input
          className="search-input"
          type="text"
          value={searchValue}
          placeholder="Search Products or Designers"
          onChange={handleChange}
        />
        <img src={searchIcon} className="search-icon" alt="search" />
      </form>

      <div className="bmh-features-headers">
        <button
          onClick={() => handleDisplay("build-it")}
          className="bmh-feature feature1-header"
        >
          BUILD IT
        </button>
        <button
          onClick={() => handleDisplay("buy-it")}
          className="bmh-feature feature2-header"
        >
          BUY IT
        </button>
        <Link to="/home" className="bmh-feature feature3-header">
          REPAIR IT
        </Link>
      </div>
      <span className="line"></span>
      {loading || !featureDisplay ? (
        <Loader />
      ) : (
        <FeatureCard {...featureDisplay} />
      )}

      <footer className="footer">
        <FooterIcon />
      </footer>
    </div>
  );
}

export default BuildItScreen;
