import React from "react";
import share from "../assets/share.svg";
import { Slider } from "./Slider";

function ProductDisplay(): JSX.Element {
  return (
    <div className="container">
      <div className="product-display">
        <p className="price">₦95,785</p>
        <div className="share-container">
          <img src={share} alt="share" />
        </div>
        <Slider />
        <div className="product-variations">
          <select name="size" id="product-sizes">
            <option value="size" className="product-options">
              Size ▾
            </option>
          </select>
          <select name="size" id="product-colours">
            <option value="colour" className="product-options">
              Colour ▾
            </option>
          </select>
        </div>
      </div>
    </div>
  );
}

export default ProductDisplay;
