import React from "react";
import DesignCard from "./DesignCard";
import { designData } from "../designData";

export default function DesignList(): JSX.Element {
  return (
    <React.Fragment>
      {/* map throug array of design cards into DesignCards component */}
      {designData.map(designItem => (
        <DesignCard key={designItem.id} {...designItem} />
      ))}
    </React.Fragment>
  );
}
