import React from "react";
import { ICart } from "../../common/types";
import "./styles.css";
import closeIcon from "../assets/close.svg";
import { ICartItemProps } from "./types";

const CartItem = ({ cartItem, displayx, handleDelete }: ICartItemProps): JSX.Element => {
  const { id, img, name, size, color, price} = cartItem;
  return (
    <section>
      <div className="cart-item">
        <img
          src={closeIcon}
          className={displayx ? "displayx" : "undisplayx"}
          alt="close"
          onClick={()=>handleDelete(id)}
        />
        <img src={img} className="cartitem-image" alt="cart-item" />
        <div className="product-info">
          <p className="product-name">{name}</p>
          <span className="color">Color : {color}</span>{" "}
          <span className="size">Size : {size}</span>
        </div>
        <h4 className="price">{price}</h4>
      </div>
      <span className="line"></span>
    
    </section>
  );
};

export default CartItem;
