import React, { ChangeEvent, FormEvent, useState } from "react";
import { Link, RouteComponentProps } from "react-router-dom";
import {Input} from 'antd';
import homeScreenImage from "./photos/home.png";
import "./MainWelcomeScreen.css";


interface IMainWelcomeScreenProps extends RouteComponentProps<any> {
  onClick?: () => void;
}

function MainWelcomeScreen(props: IMainWelcomeScreenProps) {


  const [emailAddress, setEmailAddress] = useState<string>("");
  const [rightEmailFormat, setRightEmailFormat] = useState<boolean>(true);

  const handleEmail = (e: ChangeEvent<HTMLInputElement>): void => {
    setEmailAddress(e.target.value);
  };

  const handleSubmit = (e: FormEvent): void => {
    e.preventDefault();
    //check email format
    if (!emailAddress.includes("@")) {
      setRightEmailFormat(false);
      return;
    } else {
      setRightEmailFormat(true);
      //fetch customers
      const url =
        "https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql?query=";
      const query = `{customers (email: "${emailAddress}"){email}}`;
      fetch(`${url}${query}`)
        .then((resp) => {
          //save customer email in local storage
          localStorage.setItem("customerEmail", `${emailAddress}`);
          return resp.json();
        })
        .then((d) => {
          //check to see if already a user
          if (d.data.customers[0]) {
            //navigate to login screen
            props.history.push("/login");
          } else {
            //navigate to sign up
            props.history.push("/signup");
          }
        });
    }
  };

  return (
    <div className="welcome-container">
      <img
        src={homeScreenImage}
        alt="home-screen-image"
        className="container__image"
      />
      <form onSubmit={handleSubmit} className="welcome-form">
        <Input
          type="email"
          className="mainwelcome-input"
          placeholder="Email Address"
          onChange={handleEmail}
          value={emailAddress}
        />
        <span
          className={
            rightEmailFormat ? "right-email-format" : "wrong-email-format"
          }
        >
          Enter valid Email
        </span>
        <button type="submit" className="container__btn">
          {" "}
          LOG IN / SIGN UP{" "}
        </button>
      </form>
      <Link to="/home" className="welcome-text">
        Continue as Guest
      </Link>
    </div>
  );
}
export default MainWelcomeScreen;
