import React from "react";
import { Link } from "react-router-dom";
import arrow from "../assets/back-arrow.svg";
import ctrl from "../assets/controls-alt.svg";
import Footer from "../components/FooterNav";
import "./styles.css";
import DesignList from "./DesignList";

export function Designers(): JSX.Element {
  return (
    <React.Fragment>
      <div className="design-header">
        <Link to="/build">
          <img src={arrow} alt="back-arrow" className="signup-back-arrow" />
        </Link>

        <p className="page-name">INTERIOR DESIGNERS</p>
        <Link to="#">
          <img src={ctrl} alt="controls" />
        </Link>
      </div>
      <h4 className="designTag">
        Make Yourself At Home—Top Projects Featured By Our Designers
      </h4>
      <div className="designers">
        <DesignList />
      </div>
      <div className="container">
        <Footer />
      </div>
    </React.Fragment>
  );
}
