import React from "react";
import "./styles.css";
import { Link } from "react-router-dom";
import Button from "../../home/components/Button";
import Footer from "../../designers/components/FooterNav";

export function Shipping(): JSX.Element {
  return (
    <React.Fragment>
      <div className="design-header shipping-header">
        <p className="page-name">SHIPPING INFORMATION</p>
      </div>
      <div className="container">
        <section className="shipping-options">
          <p>Shipping Options</p>
          <div>
            <input
              type="radio"
              name="shipping-type"
              value="standard"
              className="shipping-type"
            />
            <div>
              {" "}
              <p>Free Standard Shipping</p>{" "}
              <h5>delivered on or before Friday, April 2nd</h5>
            </div>
          </div>
          <div>
            <input
              type="radio"
              name="shipping-type"
              value="express"
              className="shipping-type"
            />
            <div>
              {" "}
              <p>Free Standard Shipping</p>{" "}
              <h5>delivered on or before Monday, April 5th</h5>
            </div>
          </div>
          <div>
            <input
              type="radio"
              name="shipping-type"
              value="next day"
              className="shipping-type"
            />
            <div>
              {" "}
              <p>Free Standard Shipping</p>{" "}
              <h5>delivered on or before Tuesday, March 30</h5>
            </div>
          </div>
        </section>
      </div>
      <div className="section-border-2"></div>
      <div className="container">
        <section className="shipping-options">
          <p>Shipping Options</p>
          <input
            type="text"
            name="fullName"
            className="shipping-info"
            placeholder="Full Name"
          />
          <input
            type="text"
            name="address1"
            className="shipping-info"
            placeholder="Address Line 1"
          />
          <input
            type="text"
            name="address2"
            className="shipping-info"
            placeholder="Address Line 2"
          />
          <input
            type="text"
            name="state"
            className="shipping-info"
            placeholder="State/Region"
          />
          <input
            type="text"
            name="postCode"
            className="shipping-info"
            placeholder="Post Code"
          />
          <input
            type="text"
            name="phoneNumber"
            className="shipping-info"
            placeholder="Phone Number (optional)"
          />
        </section>
      </div>
      <div className="section-border-2 sb2"></div>
      <div className="container button-container">
        <Link to="/payment">
          <Button buttonValue="CONTINUE TO PAYMENT" />
        </Link>
      </div>
      <div className="container">
        <Footer />
      </div>
    </React.Fragment>
  );
}
