import React, { useEffect, useState, MouseEvent } from "react";
import axios from "axios";
import { Link } from "react-router-dom";
import arrow from "../assets/back-arrow.svg";
import Button from "./Button";
import Like from "../assets/thumb-up.svg";
import redlike from "../assets/thumb-up-red.svg";
import Avatar from "../assets/Rectangle 61.png";
import img1 from "../assets/img1.png";
import img2 from "../assets/img2.png";
import img3 from "../assets/img3.png";
import img4 from "../assets/img4.png";
import img5 from "../assets/img5.png";
import img6 from "../assets/img6.png";
import img7 from "../assets/img7.png";
import "./styles.css";

export function Designerprofile(props: {
  location: {
    state: { email: { designerEmail: React.SetStateAction<string> } };
  };
}): JSX.Element {
  const [designerEmail, setDesingerEmail] = useState<string>("");
  const [designerData, setDesignerData] = useState({});
  const [likeState, setLikeState] = useState(false);

  useEffect((): void => {
    setDesingerEmail(props.location.state.email.designerEmail);
  });

  useEffect(() => {
    const fetchUrl =
      "https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql";
    const query = `?query={customers (email: "${designerEmail}"){id, email, products{imageCollection{url}}}}`;
    const getData = async (): Promise<void> => {
      return await axios
        .post(`${fetchUrl}${query}`, {
          headers: {
            "Content-Type": "application/GraphQL"
          }
        })
        .then(res => {
          if (res.status === 200) {
            setDesignerData(res.data.data.customers[0]);
          }
        })
        .catch(err => {
          console.log(err.message);
        });
    };

    getData();
  }, []);

  //handle like button style and count
  const handleLikes = (e: MouseEvent): void => {
    setLikeState(!likeState);
  };
  const likeIcon = likeState ? redlike : Like;

  return (
    <div className="container">
      <div className="page-name-header d-profile-header">
        <Link to="/design">
          <img
            src={arrow}
            alt="back-arrow"
            className="signup-back-arrow d-back-arrow"
          />
        </Link>

        <p className="page-name designer-name"> TEMILOLUWA YOUNG</p>
      </div>
      <div>
        <Button buttonValue="SEND ENQUIRY" />
        <button onClick={handleLikes} className="likeButton">
          <img src={likeIcon} alt="like" className="like-profile" />
        </button>
      </div>
      <div className="designer-profile">
        <img src={Avatar} alt="avatar" />
        <p>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Non ut
          adipiscing feugiat at. Massa nunc, tortor tellus dignissim purus
          semper.
          <br></br> <br></br>Quam ut nulla ut id. Justo, nunc, cursus enim
          massa. Mi, enim eget a ante ac donec aliquet quis iaculis odio viverra
          ti.
        </p>
      </div>
      <h3 className="word-banner">
        “There are two things that make a room timeless: a sense of history and
        a piece of the future”
      </h3>
      <div className="image-grid">
        <div className="grid-col-1">
          <img src={img1} />
          <img src={img2} />
        </div>
        <div className="grid-col-2">
          {" "}
          <img src={img3} />
        </div>
      </div>
      <p className="mid-section">
        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Non ut
        adipiscing feugiat at. Massa nunc, tortor tellus dignissim purus semper.{" "}
        <br></br>
        <br></br>
        Quam ut nulla ut id. Justo, nunc, cursus enim massa. Mi, enim eget a
        ante ac donec aliquet. Quis iaculis odio viverra lacus cursus augue.
        Eros, elit congue vivamus vestibulum. <br></br>
        <br></br> Elementum porta phasellus vitae accumsan a nunc lacus accumsan
        sed. Nisl consequat sit quis massa in blandit eleifend dolor
        sollicitudin. Ac quis tellus viverra a vestibulum. Ut tempor, at
        suspendisse bibendum ultrices nec tempor.
      </p>
      <div className="grid-2">
        <img src={img4} />
        <img src={img5} />
        <img src={img6} />
        <img src={img7} />
      </div>
      <div className="foot-button">
        <Button buttonValue="SEND ENQUIRY" />
      </div>
    </div>
  );
}
