import React from "react";
import { Link } from "react-router-dom";
import { IFeatureData } from "../../common/types";

function FeatureOneHomeScreenCard({
  imageCollection,
}: IFeatureData): JSX.Element {
  const path = "/";
  const service = imageCollection.map((d) => {
    return (
      <Link to= {{pathname: `${path}${d.name}`}} className="feature feature1" key={d.id}>
        <img src={d.url} alt="service" className="service" height="200px" />
        <p className="service-name">{d.name}</p>
      </Link>
    );
  });
  return <div className="buildit-features">{service}</div>;
}

export default FeatureOneHomeScreenCard;
