import React from "react";
import "./styles.css";
import { Link } from "react-router-dom";
import backArrow from "../assets/back-arrow.svg";
import thumbUp from "../assets/thumb-up.svg";
import shanePicture from "../assets/shane-picture.png";
import { IArchitectData } from "../../common/types";

function ArchitectPortfolio({ firstName, lastName, products, id }: IArchitectData): JSX.Element {
  const handleEmail=()=>{
alert("Email sent")
  }
  
  return (
    <div key = {id}className="architectPortfolio-container">
      <header className="architectPortfolio-header">
        <div className="head">
          <Link to="/architects">
            <img src={backArrow} className="back-arrow" alt="back-arrow" />
          </Link>

          <h1 className="architectPage-name">{firstName} {lastName}</h1>
        </div>
        <div className="bottom">
          <button className="sendInquiry">SEND INQUIRY</button>
          <img src={thumbUp} className="thumb-up" alt="thumb-up"/>
        </div>
      </header>
      <section className="architect-introduction">
        <img
          src={shanePicture}
          className="architect-image"
          alt="architect-image"
          height="200px"
        />
        <p className="architect-intro">
          {products[0].description}<br/>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Non ut
          adipiscing feugiat at. Massa nunc, tortor tellus dignissim purus
          semper.
          <br /> <br />
          Quam ut nulla ut id. Justo, nunc, cursus enim massa. Mi, enim eget a
          ante ac donec aliquet quis iaculis odio viverra ti.
        </p>
      </section>
      <section className="architect-philosophy">
        <p className="philosophy">
          “As an architect, I design for the present, with an awareness of the
          past for a future, which is essentially unknown”
        </p>
      </section>
      <section className="architect-designs">
        <section className="architectDesigns-firstDisplay">
          <div className="houseDisplay1">
            <img src={products[0].imageCollection[1].url} className="house-design1" alt="house1" />
            <img src={products[0].imageCollection[0].url} className="house-design2" alt="house2" />
          </div>
          <div className="houseDisplay2">
            <img src={products[0].imageCollection[7].url} className="house3" alt="house3" />
          </div>
        </section>
        <section className="architectDesigns-introduction">
          <p className="architectDesigns-intro">
          {products[0].description}<br/>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Non ut
            adipiscing feugiat at. Massa nunc, tortor tellus dignissim purus
            semper.
            <br />
            <br />
            Quam ut nulla ut id. Justo, nunc, cursus enim massa. Mi, enim eget a
            ante ac donec aliquet. Quis iaculis odio viverra lacus cursus augue.
            Eros, elit congue vivamus vestibulum.
            <br />
            <br />
            Elementum porta phasellus vitae accumsan a nunc lacus accumsan sed.
            Nisl consequat sit quis massa in blandit eleifend dolor
            sollicitudin. Ac quis tellus viverra a vestibulum. Ut tempor, at
            suspendisse bibendum ultrices nec tempor.
          </p>
        </section>
        <section className="architectDesigns-secondDisplay">
          <img src={products[0].imageCollection[3].url} className="house-design" alt="house4" />
          <img src={products[0].imageCollection[2].url} className="house-design" alt="house5" />
          <img src={products[0].imageCollection[5].url} className="house-design" alt="house6" />
          <img src={products[0].imageCollection[6].url} className="house-design" alt="house7" />
        </section>
      </section>
      <button className="footer-sendInquiry" onClick={handleEmail}>SEND INQUIRY</button>
    </div>
  );
}

export default ArchitectPortfolio;
