import React, { ChangeEvent, FormEvent, useState } from "react";
import { Link } from "react-router-dom";
import {Input} from 'antd'
import arrow from "../assets/back-arrow.svg";
import "./SigninPage.css";
import { auth } from "../../../firebase";

function Signin(props: any): JSX.Element {
  const [password, setPassword] = useState<string>("");
  const [repassword, setRePassword] = useState("");
  const [passwordLength, setPasswordLength] = useState<boolean>(true);
  const [passwordMatch, setPasswordMatch] = useState<boolean>(true);
  const [
    wrongPasswordErrorMessage,
    setWrongPasswordErrorMessage,
  ] = useState<boolean>(true);

  //handle password onChange
  const handlePassword = (e: ChangeEvent<HTMLInputElement>): void => {
    setPassword(e.target.value);
  };
  //handle re-typed password onChange
  const handleRePassword = (e: ChangeEvent<HTMLInputElement>): void => {
    setRePassword(e.target.value);
  };

  //handle password submit
  const handleSignin = (e: FormEvent): void => {
    e.preventDefault();
    //set password length
    const passwordNumber = 8;
    //check password length
    if (password.length < passwordNumber) {
      setPasswordLength(false);
      return;
    }
    //check password match
    else if (password !== repassword) {
      setPasswordMatch(false);
      return;
    } else {
      //get customer email from local storage
      const emailAddress: any = localStorage.getItem("customerEmail");
      //authenticate user 
      auth
        .signInWithEmailAndPassword(emailAddress, password)
        .then((auth: any) => {
          if (!auth) {
            setWrongPasswordErrorMessage(false);
            return;
          } else {
            props.history.push("/home");
          }
        });
      }
  };

  return (
    <div className="container">
      <div className="page-name-header">
        <Link to="/welcome">
          <img src={arrow} alt="back-arrow" className="back-arrow" />
        </Link>

        <p className="page-name">LOG IN</p>
      </div>
      <span className="line"></span>

      <form onSubmit={handleSignin} className="signin-form">
        <h1>Welcome back!</h1>
        <p className="signin-text">Enter your password to continue</p>
        <Input.Password
          type="password"
          className="input"
          value={password}
          placeholder="Password"
          onChange={handlePassword}
          name="password"
        />
        <span
          className={
            passwordLength
              ? "correct-password-length"
              : "incorrect-password-length"
          }
        >
          Must be at least 8 characters{" "}
        </span>
        <span
          className={
            wrongPasswordErrorMessage
              ? "no-password-errormessage"
              : "password-errormessage"
          }
        >
          Wrong Email or Password
        </span>
        <Input.Password
          type="password"
          className="input"
          value={repassword}
          placeholder="Re-type password"
          onChange={handleRePassword}
          name="repassword"
        />
        <span
          className={
            passwordMatch ? "signin-password-match" : "signin-password-unmatch"
          }
        >
          Passwords do not match
        </span>
        <p className="signin-text">Must be at least 8 characters</p>

        <button type="submit" className="signin-btn">
          LOG IN
        </button>
      </form>
      <Link to="/" className="forgotpassword">
        Forgot password?
      </Link>
    </div>
  );
}

export default Signin;
