import React from "react";
import "./styles.css";
import {Link} from "react-router-dom";
import card1Img from "../assets/cardimg.png";
import card3Img from "../assets/card2img.png";
import card4Img from "../assets/card4img.png";
import NewItems from "./NewItems";
import Button from "./Button";

export function Home(): JSX.Element {
  return (
    <React.Fragment>
      <div className="homeScreen">
        <div className="flashPrompt">
          <span id="red">30% </span> off BMH Thrifted Items. Learn more
        </div>
        <div className="card card1">
          <img src={card1Img} className="card1Img" alt="solid surface" />
          <h4 className="cardTitle">
            Solid Surface, Reimagined— Irresistably Matte Hues Meet New Age
            Function
          </h4>
          <p>By Temiloluwa Young</p>
          <Link to ="/build"><Button buttonValue="VIEW PORTFOLIO" /></Link>
          
        </div>
        <NewItems />
        <div className="card card3">
          <h4 className="cardTitle card3Title">
            Groundbreaking: 5-Minute Plan to Build A Home That Is Uniquely
            Yours!
          </h4>
          <img src={card3Img} alt="card 3 image" className="card1Img" />
          <Button buttonValue="SEND INQUIRY" />
        </div>
        <div className="card card4">
          <h4 className="cardTitle">Exclusive: BMH x Armstrong Nnadi </h4>
          <p>
            A Vibrant Retrofit Breathing New Life Into Dilapidated 19th-Century
            Houses in Lagos
          </p>
          <a href="#">PREVIEW COLLECTION</a>
          <img src={card4Img} alt="" className="card1Img" />
        </div>
      </div>
    </React.Fragment>
  );
}
