import React, { useEffect, useState } from "react";
import "./styles.css";
import ArchitectPortfolio from "./ArchitectPortfolio";
import { ISpecificArchitectProps } from "../types";
import { IArchitectData } from "../../common/types";
import Loader from "../../Loader";

function SpecificArchitect({}: ISpecificArchitectProps) {
  const [loading, setLoading] = useState(true);
  const [architectData, setArchitectData] = useState<IArchitectData | null>(
    null
  );

  const handlePortfolio = () => {
    const url =
      "https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql?query=";
    const query = `{ customers(email: "shane@bmh.com") {
        id
        firstName
        lastName
        address
        email
        products {
          name
          description
          status
          userId
          likes
          imageCollection {
            id
            name
            description
            status
            likes
            url
          }
        }
      }}`;
    fetch(`${url}${query}`)
      .then((resp) => resp.json())
      .then((d) => {
        setLoading(false);
        setArchitectData(d.data.customers[0]);
      });
  };

  useEffect(() => {
    handlePortfolio();
  }, []);

  return (
    <div>
      {loading||!architectData? <Loader/> : <ArchitectPortfolio {...architectData} /> }
    </div>
  )
  
}

export default SpecificArchitect;
