import React, { useState, MouseEvent } from "react";
import { Link } from "react-router-dom";
import arrow from "../assets/back-arrow.svg";
import fav from "../assets/favourite.svg";
import redFav from "../assets/red-fav.svg";
import { ProductDetail } from "./ProductDetail";
import ProductDisplay from "./ProductDisplay";
import Button from "./Button";
import Footer from "../../designers/components/FooterNav";
import "./styles.css";

export function Product(): JSX.Element {
  const [liked, setLiked] = useState(false);
  const [added, setAdded] = useState(false);

  const toggleFav = (e: MouseEvent): void => {
    setLiked(!liked);
  };
  const handleAddItem = (e: MouseEvent): void => {
    setAdded(true);
  };
  const handleNotif = (e: MouseEvent): void => {
    setAdded(false);
  };

  const likeIcon = liked ? redFav : fav;
  const showNotification = added ? "added-notification" : "no-notif";

  return (
    <div className="product-container">
      <div className={showNotification}>
        Item has been added to cart{" "}
        <Link to="/shopping">
          <button onClick={handleNotif}>VIEW</button>
        </Link>
      </div>
      <div className="design-header product-header">
        <Link to="/allproducts">
          <img src={arrow} alt="back-arrow" className="signup-back-arrow" />
        </Link>

        <p className="page-name">Minge Large Mirror</p>
        <button onClick={toggleFav}>
          <img src={likeIcon} alt="favorite" />
        </button>
      </div>

      <ProductDisplay />

      <hr className="section-border"></hr>
      <Button handleAddItem={handleAddItem} />
      <div className="section-border-2"></div>
      <ProductDetail />
      <div className="container">
        <Footer />
      </div>
    </div>
  );
}
