import React from "react";
import swivel from "../../home/assets/item1.png";
import crate from "../../home/assets/item2.png";
import copper from "../../home/assets/item3.png";
import heart from "../../home/assets/Shape.png";
import "./styles.css";

export default function RecomendedItems(): JSX.Element {
  return (
    <React.Fragment>
      <div className="card2 recomended-container">
        <p className="recomended">Recomended</p>
        <div className="horizontalScroll">
          <div className="itemCard">
            <img className="itemImage" src={swivel} alt="Swivel Chair" />
            <p className="itemName">Bob Williams Swivel Chair</p>
            <p className="itemPrice">₦100,435</p>
          </div>
          <div className="itemCard">
            {" "}
            <img className="itemImage" src={crate} alt="Swivel Chair" />
            <p className="itemName">Crate Wooden Table</p>
            <p className="itemPrice">₦89,950</p>
          </div>
          <div className="itemCard">
            <img className="itemImage" src={copper} alt="Swivel Chair" />
            <p className="itemName">Copper Studio Chair</p>
            <p className="itemPrice">₦107,555</p>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
