import React, { useEffect, useState } from "react";
import Footer from "../../footericon/components/FooterIcon";
import Loader from "../../Loader";
import AllProductFilter from "./AllProductFilter";
import AllProductCard from "./AllProductCard";
import searchIcon from "../assets/search.svg";
import backArrow from "../assets/back-arrow.svg";
import "./styles.css";
import { IAllProductCardProps } from "./types";
import { IAllProduct } from "../../common/types";
import { Link } from "react-router-dom";

const AllProductScreen = ({}: IAllProductCardProps): JSX.Element => {
  const [loading, setLoading] = useState(true);
  const [allproductsData, setAllProductsData] = useState<IAllProduct | null>(
    null
  );

  const getAllProducts = () => {
    const url =
      "https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql?query=";
    const query = `{
  products(status: "all products"){
  id
  name
  status
  price
  userId
  imageCollection{
    id
    name
    url
  }
  }
  }`;
    fetch(`${url}${query}`)
      .then((resp) => resp.json())
      .then((d) => {
        setLoading(false);

        setAllProductsData(d.data);
      });
  };

  useEffect(() => {
    getAllProducts();
  }, []);

  return (
    <div className="allproducts-container">
      <header className="header">
        <Link to="/collection">
          <img src={backArrow} className="back-arrow" alt="back-arrow" />
        </Link>
        <h1 className="allproductspage-name">ALL PRODUCTS</h1>
        <img
          src={searchIcon}
          className="searchproduct-icon"
          alt="search-icon"
        />
      </header>
      <AllProductFilter />
      {loading || !allproductsData ? (
        <Loader />
      ) : (
        <AllProductCard {...allproductsData} />
      )}
      <footer className="footer">
        <Footer />
      </footer>
    </div>
  );
};

export default AllProductScreen;
