import React, { useState, FormEvent } from "react";
import { Redirect, Link } from "react-router-dom";
import arrow from "../assets/back-arrow.svg";
import { userTypes } from "../types";
import { Input } from "antd";
import { auth } from "../../../firebase";

import "./styles.css";
import Axios from "axios";
import Button from "./Button";

export function SignUp(): JSX.Element {
  const [password, setPassword] = useState("");
  const [repassword, setRePassword] = useState("");
  const [isSignedUp, setIsSignedUp] = useState(false);
  const [uid, setUid] = useState<string>("");

  //collect password value
  function handlePassword(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setPassword(e.target.value);
  }
  //collect retyped password value
  function handleRePassword(e: {
    target: { value: React.SetStateAction<string> };
  }): void {
    setRePassword(e.target.value);
  }
  //check password meets validation
  const passwordLength: boolean = password.length >= 8;
  //check retyped password matches initial password
  const passwordMatch: boolean = password === repassword;
  //check password meets both requirments
  const validatePassword: boolean = passwordLength && passwordMatch;

  const handleSubmit = (e: FormEvent): void => {
    e.preventDefault();
    if (validatePassword) {
      //retreive user enail from local storage
      const userEmail: any = localStorage.getItem("customerEmail");
      //create user object with email and password
      const newUser: userTypes = {
        email: userEmail,
        password: password
      };
      //signup endpoint
      const signUpUrl = `https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/signup`;
      //sign up requests
      const signedUp = (): Promise<void> => 
      auth
        .createUserWithEmailAndPassword(userEmail, password)
        .then((auth: any) => {
          //update user schema with new user upon successful signup
          if (auth) {
            setUid(auth.user.uid);
            const query = `?query=mutation{addUser(email:"${newUser.email}", uid: "${auth.user.uid}"){email}}`;
            const url = `https://us-central1-build-myhouse.cloudfunctions.net/bmhAPi/graphql`;

            return Axios.post(`${url}${query}`, {
              headers: {
                "Content-Type": "application/GraphQL"
              }
            })
              .then(res => {
                if (res.status === 200) {
                  //indicate successful signup and schema update
                  setIsSignedUp(true);
                }
              })
              .catch(err => {
                console.log(err.message);
              });
          }
        });
      signedUp();
    }
  };

  const buttonStyle: string = validatePassword ? "formSubmit" : "noSubmit";

  //style validators for password and re-type password
  const inputPassword: string = passwordLength
    ? "rightInput"
    : password.length === 0
    ? "formText"
    : "wrongInput";
  const inputRePassword: string = validatePassword
    ? "rightInput"
    : repassword.length === 0
    ? "formText"
    : "wrongInput";

  if (isSignedUp) {
    // redirect to  homepage upon succesful signup flow
    return <Redirect to={{ pathname: "/Home" }} />;
  }
  return (
    //if user isnt signed up, maintain signup form
    <React.Fragment>
      <div className="page-name-header">
        <Link to="/welcome">
          <img src={arrow} alt="back-arrow" className="signup-back-arrow" />
        </Link>

        <p className="page-name">SIGN UP</p>
      </div>
      <div className="formWrapper">
        <form className="signUpForm" onSubmit={handleSubmit}>
          <h1 className="signHeader">Welcome!</h1>
          <p className="prompt" id="prompt1">
            Create a password to continue
          </p>
          <Input.Password
            className={inputPassword}
            type="password"
            name="password"
            id="password"
            placeholder="  Password"
            value={password}
            onChange={handlePassword}
          />
          <Input.Password
            className={inputRePassword}
            type="password"
            name="re-password"
            id="re-password"
            placeholder="  Re-type password"
            value={repassword}
            onChange={handleRePassword}
          />
          <span className="prompt">must at least be 8 characters</span>
          <Button
            buttonStyle={buttonStyle}
            validatePassword={validatePassword}
          />
          <span className="policy">
            By signing up you agree to our <b>privacy policy </b>and{" "}
            <b>terms of use</b>.
          </span>
        </form>
      </div>
    </React.Fragment>
  );
}
