import React, { ChangeEvent, FormEvent, useState } from "react";
import "./styles.css";
import homeIcon from "../assets/home-icon.svg";
import { Link } from "react-router-dom";
import Footer from "../../footericon/components/FooterIcon";

const Payment = (): JSX.Element => {
  const [name, setName] = useState("Jamie Obiri");
  const [street, setStreet] = useState("12 Edmonson Street");
  const [city, setCity] = useState("Ikoyi, Lagos");
  const [customerState, setcustomerState] = useState("Lagos, State");
  const [zipCode, setZipCode] = useState("90021");
  const [phoneNumber, setPhoneNumber] = useState("080 23478 0929");
  const [disabled, setDisabled] = useState(true);
  const [radio, setRadio] = useState("");

  const handleEdit = () => {
    setDisabled(false);
  };
  const handleRadioChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setRadio("address");
  };
  const changeAddress = (e: FormEvent): void => {
    e.preventDefault();
  };
  const handleNameChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setName(e.target.value);
  };
  const handleStreetChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setStreet(e.target.value);
  };
  const handleCityChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setCity(e.target.value);
  };
  const handleStateChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setcustomerState(e.target.value);
  };
  const handleZipChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setZipCode(e.target.value);
  };
  const handlePhoneChange = (e: ChangeEvent<HTMLInputElement>): void => {
    setPhoneNumber(e.target.value);
  };

  return (
    <section className="payment-container">
      <h1 className="paymentpage-name">PAYMENT</h1>
      <span className="line"></span>

      <section className="billing-info">
        <div className="billing-header">
          <h4 className="billing">Billing Address</h4>
          <span onClick={handleEdit} className="edit">
            Edit
          </span>
        </div>
        <form className="billing-address" onSubmit={changeAddress}>
          <input
            type="radio"
            name="radio"
            value={radio}
            onChange={handleRadioChange}
          />
          <div>
            <input
              className="billinginfo-input customer-name"
              disabled={disabled}
              onChange={handleNameChange}
              type="text"
              name="name"
              placeholder="name"
              value={name}
            />
            <input
              className="billinginfo-input"
              disabled={disabled}
              onChange={handleStreetChange}
              type="text"
              name="Street"
              placeholder="Street"
              value={street}
            />
            <input
              className="billinginfo-input"
              disabled={disabled}
              onChange={handleCityChange}
              type="text"
              name="city"
              placeholder="City,State"
              value={city}
            />
            <input
              className="billinginfo-input"
              disabled={disabled}
              onChange={handleStateChange}
              type="text"
              name="customerState"
              placeholder="State"
              value={customerState}
            />
            <input
              className="billinginfo-input"
              disabled={disabled}
              onChange={handleZipChange}
              type="text"
              name="zipCode"
              placeholder="Zip Code"
              value={zipCode}
            />
            <input
              className="billinginfo-input"
              disabled={disabled}
              onChange={handlePhoneChange}
              type="text"
              name="phoneNumber"
              placeholder="Phone Number"
              value={phoneNumber}
            />
          </div>
        </form>
      </section>
      <span className="offer-line"></span>
      <section className="payment-type">
        <button className="pay-btn">
          <img src={homeIcon} className="paymenthome-icon" alt="home-icon" />
          Pay via Bank Transfer
        </button>
        <button className="pay-btn">PayStack</button>
      </section>
      <span className="offer-line"></span>
      <section className="totalorder-details">
        <div className="ship-info">
          <p className="ship">Shipping fee</p>
          <p className="subtotal-price"> 13,999</p>
        </div>
        <span className="payment-line"></span>
        <div className="ship-info">
          <p className="sub-total">sub total</p>
          <p className="subtotal-price"> 13,999</p>
        </div>
        <span className="payment-line"></span>
        <div className="ship-info">
          <p className="total">Total</p>
          <p className="total-price"> 13,999</p>
        </div>
      </section>
      <span className="shipping-line"></span>
      <Link to="/order">
        <button className="place-order">PLACE YOUR ORDER</button>
      </Link>

      <Footer />
    </section>
  );
};

export default Payment;
