import React, { useState } from "react";
import dropDown from "../assets/drop-down.svg";
import "./styles.css";

const AllProductFilter = (): JSX.Element => {


  const handleSort = () => {
   alert("sort")
    
  };
  const handleFilter = () => {
    alert("filter")
  };
  return (
    <div className="allproductsfilter-container">
      <button className="sort" onClick={handleSort}>
        <span className="filter-text">Sort</span>
        <img src={dropDown} className="sort-dropdown" alt="drop-down" />
      </button>
      <button className="filter" onClick={handleFilter}>
        <span className="filter-text">Filter</span>
        <img src={dropDown} className="filter-dropdown" alt="drop-down" />
      </button>
    </div>
  );
};

export default AllProductFilter;
