import React, { useState } from "react";
import { IAllProduct } from "../../common/types";
import { Link } from "react-router-dom";
import heartShape from "../assets/heart-shape.svg";
import redHeartShape from "../assets/redheart-shape.svg";
import chair from "../assets/chair.png";

import "./styles.css";

const AllProductCard = ({ products }: IAllProduct): JSX.Element => {
  const [likedProducts, setLikedProducts] = useState<number[]>([]);

  const handleLiked = (id: number) => {
    if (!likedProducts.includes(id)) {
      setLikedProducts([...likedProducts, id]);
    } else {
      const newLikedProducts = likedProducts.filter(
        productId => productId != id
      );
      setLikedProducts(newLikedProducts);
    }
  };

  const productCard = products.map(d => {
    return (
      <button className="product-card" key={d.id}>
        <img
          src={d.imageCollection[0] ? d.imageCollection[0].url : chair}
          alt="product-image"
          className="allproduct-image"
          height="200px"
        />
        <img
          src={likedProducts.includes(d.id) ? redHeartShape : heartShape}
          className={"liked"}
          alt="liked"
          onClick={() => handleLiked(d.id)}
        />
        <Link to="/productinfo">
          <p className="product-name">{d.name}</p>
        </Link>

        <p className="product-price">₦{d.price}</p>
      </button>
    );
  });
  return <div className="products-container">{productCard}</div>;
};

export default AllProductCard;
