import React, { useEffect, useState } from "react";
import CartItem from "./CartItem";
import "./styles.css";
import { Link } from "react-router-dom";
import editBasket from "../assets/edit-basket.svg";
import backArrow from "../assets/back-arrow.svg";
import Footer from "../../footericon/components/FooterIcon";
import Loader from "../../Loader";
import quilt from "../assets/quilt.png";
import mirror from "../assets/mirror.png";
import Suggested from "./Suggested";
import { ICartItemProps } from "./types";
import { ICart } from "../../common/types";

const CartScreen = ({}: ICartItemProps): JSX.Element => {
  const [loading, setLoading] = useState(true);
  const [cart, setCart] = useState<ICart[] | null>(null);
  const [displayx, setDisplayx] = useState(false);
  const cartItems = [
    {
      img: quilt,
      name: "Channel Quilted Coverlet",
      size: "Queen",
      color: "Velvet Blue",
      price: "₦95,785",
      id: 1,
    },
    {
      img: mirror,
      name: "Ming Large Mirror",
      size: "107x400cm",
      color: "Gold Metal",
      price: "₦95,785",
      id: 2,
    },
    {
      img: quilt,
      name: "Channel Quilted Coverlet",
      size: "Queen",
      color: "Velvet Blue",
      price: "₦95,785",
      id: 3,
    },
  ];

  const handleCart = () => {
    setCart(cartItems);
    setLoading(false);
  };
  const handleDelete = (id: any) => {
    const filteredCart = cart?.filter((filtered) => {
      return filtered.id !== id;
    });

    if (!filteredCart) {
      <Loader />;
    } else setCart(filteredCart);
  };

  const handleCloseDisplay = () => {
    if (displayx) {
      setDisplayx(false);
    } else {
      setDisplayx(true);
    }
  };

  useEffect(() => {
    handleCart();
  }, []);

  return (
    <div className="cartscreen-container">
      <header className="cartscreen-header">
        <Link to="/productinfo">
          <img src={backArrow} className="back-arrow" alt="back-arrow" />
        </Link>
        <h1 className="page-name">SHOPPING CART</h1>
        <img
          src={editBasket}
          onClick={handleCloseDisplay}
          className="edit-basket"
          alt="edit-basket"
        />
      </header>
      <span className="line"></span>
      {loading || !cart ? (
        <Loader />
      ) : (
        cart.map((cartItem) => (
          <CartItem
            key={cartItem.id}
            cartItem={cartItem}
            displayx={displayx}
            handleDelete={handleDelete}
          />
        ))
      )}
      <div className="cartsub-total">
        <p className="subtotal">Cart Subtotal</p>
        <p className="totalprice">₦395,785</p>
      </div>
      <span className="line"></span>
      <Link to="/payment">
      <button className="checkout-btn">CHECKOUT</button>
      </Link>
      <span className="offer-line"></span>
      <p className="special-offer">
        Special Offer!{" "}
        <span className="offer-code">Use Abaku10 for 10% off.</span>
      </p>
      <span className="offer-line"></span>
      <Suggested />
      <Footer />
    </div>
  );
};

export default CartScreen;
