import React, { useEffect } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./App.css";

import SigninPage from "./components/signin/components/SigninPage";
import MainWelcomeScreen from "./components/MainWelcomeScreen";
import { SignUp } from "./components/signup/components/SignUp";
import { Home } from "./components/home/components/Home";
import FeatureHomeScreen from "./components/buildit/components/FeatureHomeScreen";
import ArchitecturalDesign from "./components/architecturaldesign/components/ArchitecturalDesign";
import { Planning } from "./components/planning/components/Planning";
import { Designers } from "./components/designers/components/Designers";
import { Designerprofile } from "./components/designProfile/components/designerProfile";
import { Collection } from "./components/collection/components/Collection";
import ShoppingCart from "./components/shoppingcart/components/CartScreen";
import { Product } from "./components/productDescription/components/Product";
import SpecificArchitect from "./components/architectportfolio/components/SpecificArchitect";
import { Shipping } from "./components/shipping/components/Shipping";
import AllProductScreen from "./components/allproductscreen/components/AllProductScreen";
import { OrderReview } from "./components/orderReview/components/OrderReview";
import Payment from "./components/payment/components/Payment";

function App(): JSX.Element {
  useEffect(() => {
    const loadingEl = document.querySelector(".app-loader");
    loadingEl?.classList.add("app-loader--hidden");
  }, []);

  return (
    <Router>
      <div className="App">
        <Switch>
          <Route exact path="/" component={MainWelcomeScreen} />
          <Route exact path="/home" component={Home} />
          <Route exact path="/signup" component={SignUp} />
          <Route exact path="/login" component={SigninPage} />
          <Route path="/build" component={FeatureHomeScreen} />
          <Route
            exact
            path="/architectural design"
            component={ArchitecturalDesign}
          />
          <Route exact path="/architects/:id" component={SpecificArchitect} />
          <Route exact path="/planning" component={Planning} />
          <Route exact path="/interior decor" component={Designers} />
          <Route exact path="/designer" component={Designerprofile} />
          <Route exact path="/collection" component={Collection} />
          <Route exact path="/productinfo" component={Product} />
          <Route exact path="/shipping" component={Shipping} />
          <Route exact path="/allproducts" component={AllProductScreen} />
          <Route exact path="/payment" component={Payment} />
          <Route exact path="/shopping" component={ShoppingCart} />
          <Route exact path="/order" component={OrderReview} />
        </Switch>
      </div>
    </Router>
  );
}

export default App;
