import React from "react";
import "./styles.css";
import FooterIcon from "../../footericon/components/FooterIcon";
import { Link } from "react-router-dom";
import backArrow from "../assets/back-arrow.svg";
import controls from "../assets/controls-alt.svg";
import thumbUp from "../assets/thumb-up.svg";
import design1 from "../assets/design1.png";
import design2 from "../assets/design2.png";
import design3 from "../assets/design3.png";
import design4 from "../assets/design4.png";

function ArchitectList(props: any): JSX.Element {
  return (
    <div className="architeuralDesign-container">
      <header className="architectList-header">
        <Link to="/build">
        <img src={backArrow} className="back-arrow" alt="back-arrow" />
        </Link>
        
        <h1 className="page-name">ARCHITECTS</h1>
        <img src={controls} className="controls" alt="controls" />
      </header>
      <span className="line"></span>
      <section className="designs">
        <p className="design-text">Make Yourself At Home—Top Projects Featured By Our Architects</p>
        <div className="design">
          <img src={design1} className="design-image" alt="design" height="200px" />
          <div className="design-description">
            <p className="design-name">
              Reinvogarated Helmock Wood With an Element of Surprise
            </p>
            <div className="design-likes">
              <img src={thumbUp} className="thumb-up" alt="thumb" />
              <span className="numberOfLikes">235</span>
            </div>
          </div>
          <p className="architect">
            By <Link to="/architects/portfolio" className="architect-name">BECKY DIXON-TANGAR</Link>{" "}
          </p>
          <span className="design-line"></span>
        </div>

        <div className="design">
          <img src={design2} className="design-image" alt="design" height="200px" />
          <div className="design-description">
            <p className="design-name">
              Reinvogarated Helmock Wood With an Element of Surprise
            </p>
            <div className="design-likes">
              <img src={thumbUp} className="thumb-up" alt="thumb" />
              <span className="numberOfLikes">140</span>
            </div>
          </div>
          <p className="architect">
          By
          <Link to="/architects/portfolio" onClick={props.handlePortfolio} className="architect-name">UGOCHUKWU SHANE</Link  >
           
          </p>
          <span className="design-line"></span>
        </div>

        <div className="design">
          <img src={design3} className="design-image" alt="design" height="200px" />
          <div className="design-description">
            <p className="design-name">
              Reinvogarated Helmock Wood With an Element of Surprise
            </p>
            <div className="design-likes">
              <img src={thumbUp} className="thumb-up" alt="thumb" />
              <span className="numberOfLikes">428</span>
            </div>
          </div>
          <p className="architect">
            By <Link to="/architects/portfolio" className="architect-name">CHINAZOR FORD</Link>{" "}
          </p>
          <span className="design-line"></span>
        </div>

        <div className="design">
          <img src={design4} className="design-image" alt="design" height="200px"/>
          <div className="design-description">
            <p className="design-name">
              Reinvogarated Helmock Wood With an Element of Surprise
            </p>
            <div className="design-likes">
              <img src={thumbUp} className="thumb-up" alt="thumb" />
              <span className="number-of-likes">342</span>
            </div>
          </div>
          <p className="architect">
            By <Link to="/architects/portfolio" className="architect-name">ONYINYE OKOLI</Link>{" "}
          </p>
        </div>
      </section>
      <footer className="footer">
        <FooterIcon />
      </footer>
    </div>
  );
}

export default ArchitectList;
