import React from "react";
import { Link } from "react-router-dom";
import homeIcon from "../assets/home-icon.svg";
import categoriesIcon from "../assets/category-icon.svg";
import cartBag from "../assets/cartBag-icon.svg";
import userIcon from "../assets/user-icon.svg";

export default function FooterNav(): JSX.Element {
  return (
    <footer className="footer-nav">
      <Link to="/home" className="nav-icon home-icon">
        <img src={homeIcon} alt="home" />
        <p className="icon-name">Home</p>
      </Link>
      <Link to="/build" className="nav-icon categories-icon">
        <img src={categoriesIcon} alt="categories" />
        <p className="icon-name">Categories</p>
      </Link>
      <Link to="#" className="nav-icon cart-icon">
        <img src={cartBag} alt="cart" />
        <p className="icon-name">Shopping</p>
      </Link>
      <Link to="#" className="nav-icon account-icon">
        <img src={userIcon} alt="account" />
        <p className="icon-name">My Account</p>
      </Link>
    </footer>
  );
}
