import React, { useEffect, useState, MouseEvent } from "react";
import { Link } from "react-router-dom";
import like from "../assets/thumb-up.svg";
import redlike from "../assets/thumb-up-red.svg";
import { cardPropTypes } from "../types";
import "./styles.css";

//define the UI and elements of individual card item with specified properties
//props passed from DesignList component

const DesignCard = (props: cardPropTypes): JSX.Element => {
  const designDetail = props;
  const [likeState, setLikeState] = useState(false);
  const [likeCounts, setLikeCounts] = useState(designDetail.likes);

  const designerEmail = designDetail.email;

  //handle like button style and count
  const handleLikes = (e: MouseEvent): void => {
    setLikeState(!likeState);

    !likeState ? setLikeCounts(likeCounts + 1) : setLikeCounts(likeCounts - 1);
  };
  const redir = designerEmail === "" ? "/design" : "/designer";
  const likeIcon = likeState ? redlike : like;

  return (
    <React.Fragment>
      <div className="designWrapper">
        <div className="designCard">
          <img src={designDetail.imageCard} className="designImage" />
          <div className="designMid">
            <p>{designDetail.caption}</p>
            <div className="likes">
              <button onClick={handleLikes}>
                <img src={likeIcon} alt="like" />
              </button>
              {likeCounts}
            </div>
          </div>
          <p>
            By{" "}
            <Link
              to={{
                pathname: `${redir}`,
                state: { email: { designerEmail } }
              }}
            >
              {designDetail.designer}
            </Link>
          </p>
        </div>
      </div>
    </React.Fragment>
  );
};

export default DesignCard;
