import React from "react";
import swivel from "../assets/item1.png";
import crate from "../assets/item2.png";
import copper from "../assets/item3.png";
import heart from "../assets/Shape.png";
import "./styles.css";

export default function NewItems(): JSX.Element {
  return (
    <React.Fragment>
      <div className="card2">
        <div className="card2Banner">
          <p>What&apos;s New</p>
          <a href="#">VIEW ALL</a>
        </div>
        <p>Get ahead of the style curve with our latest arrivals</p>
        <div className="horizontalScroll">
          <div className="itemCard">
            <img className="likeIcon" src={heart} alt="like" />
            <img className="itemImage" src={swivel} alt="Swivel Chair" />
            <p className="itemName">Bob Williams Swivel Chair</p>
            <p className="itemPrice">₦100,435</p>
          </div>
          <div className="itemCard">
            <img className="likeIcon" src={heart} alt="like" />
            <img className="itemImage" src={crate} alt="Swivel Chair" />
            <p className="itemName">Crate Wooden Table</p>
            <p className="itemPrice">₦89,950</p>
          </div>
          <div className="itemCard">
            <img className="likeIcon" src={heart} alt="like" />
            <img className="itemImage" src={copper} alt="Swivel Chair" />
            <p className="itemName">Copper Studio Chair</p>
            <p className="itemPrice">₦107,555</p>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}
