import React, { useState, MouseEvent } from "react";
import { Link } from "react-router-dom";

export function ProductDetail(props: any): JSX.Element {
  const [info1, setInfo1] = useState(false);
  const [info2, setInfo2] = useState(false);
  const [info3, setInfo3] = useState(false);

  const handleInfo1 = (e: MouseEvent): void => {
    setInfo1(!info1);
  };
  const handleInfo2 = (e: MouseEvent): void => {
    setInfo2(!info2);
  };
  const handleInfo3 = (e: MouseEvent): void => {
    setInfo3(!info3);
  };

  const infoIcon1 = info1 ? "▴" : "▾";
  const infoIcon2 = info2 ? "▴" : "▾";
  const infoIcon3 = info3 ? "▴" : "▾";

  const productDetail1 = info1
    ? "product-info-display"
    : "product-info-nodisplay";
  const productDetail2 = info2
    ? "product-info-display"
    : "product-info-nodisplay";
  const productDetail3 = info3
    ? "product-info-display"
    : "product-info-nodisplay";
  return (
    <ul className="collectionList product-info">
      <li>
        <button onClick={handleInfo1}>
          Description <Link to="/productInfo">{infoIcon1}</Link>
        </button>
        <div className={productDetail1}>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. Dolore
          eveniet molestias saepe error commodi natus aut facere tenetur
          sapiente cum magnam vel necessitatibus eaque, tempore voluptate
          numquam iusto optio consequatur.
        </div>
      </li>
      <li>
        <button onClick={handleInfo2}>
          Care Information <Link to="/productInfo">{infoIcon2}</Link>
        </button>
        <div className={productDetail2}>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. Dolore
          eveniet molestias saepe error commodi natus aut facere tenetur
          sapiente cum magnam vel necessitatibus eaque, tempore voluptate
          numquam iusto optio consequatur.
        </div>
      </li>
      <li>
        <button onClick={handleInfo3}>
          Shipping <Link to="/productInfo">{infoIcon3}</Link>
        </button>
        <div className={productDetail3}>
          Lorem, ipsum dolor sit amet consectetur adipisicing elit. Dolore
          eveniet molestias saepe error commodi natus aut facere tenetur
          sapiente cum magnam vel necessitatibus eaque, tempore voluptate
          numquam iusto optio consequatur.
        </div>
      </li>
    </ul>
  );
}
